$mainColor : #ff0000;


@mixin 함수() {
    max-width: 500px;
    width: 100%;
    padding: 20px;
    margin: auto;
    background-color: #de3;
    border-radius: 10px;
}

.red {
    color: $mainColor;
}

.my-alert {
    max-width: 500px;
    width: 100%;
    padding: 20px;
    margin: auto;
    background-color: #eee;
    border-radius: 10px;
}
.my-alert.on {
    display: none
}
.my-alert2 {
    @extend .my-alert;
    background-color: #ff0;
}
.my-alert3 {
    @include 함수();
}
.my-alert p {
    margin-bottom: 0;
}